$backgroundColor: #ffffff;
$bodyColor: #000000;

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: 'PT Sans', sans-serif;
}

:root
{
  --gap: 3rem;
}

.navbar
{
  background-color: #333333;
  padding: 0 var(--gap);
  margin: 0 0 var(--gap);
  height: 56px;

  .navbar-collapse
  {
    height: 100%;
    justify-content: space-between;
  }

  .navbar-brand
  {
    color: white;
    display: flex;
    justify-content: end;
  }

  .navbar-toggler {
    color: white;
  }

 

  .navbar-nav
  {
    height: 100%;
    display: flex;
    align-items: center;
    width: 100%; 

    .nav-item
    {
      height: 100%;
      display: flex;
      position: relative;

       &.active::after
      {
          content: "";
          position: absolute;
          bottom: 0px;
          left: 4px;
          right: 4px;
          height: 4px;
          background-color: #ab0606;
          border-top-left-radius: 1px;
          border-top-right-radius: 1px;
      }  
    }
  }

  .nav-link
  {
    flex: 1;
    width: 100%;
    font-weight: 500;
    font-size: 18px;
    height: 100%;
    color: #fff;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    overflow: hidden;
    font-family: Spectral;
    padding: 24px 18px;

    &:hover
    {
      color: white;
      text-decoration: underline; 
    }
  } 
}

.color-item
  {
    color: #ab0606;
  }

  .list1b 
  {
    margin: 20px 0 0;
    padding:0;
    list-style: none;
    counter-reset: li;
  }
  .list1b li 
  {
      border: 2px solid #DDDDDD;
      background: #FAFAFA;
      position: relative;
      margin-bottom: 20px;
      padding: 16px 10px;
      -webkit-transition-duration: 0.4s;
      transition-duration: 0.4s;
  }
  .list1b li:hover 
  {
      z-index: 1;
      border: 2px solid #ab0606;
  }
  .list1b li:hover:before 
  {
      border: 2px solid #780404;
      background: #780404;
      color: #FFF;
      -webkit-transform: translate(-12px, 0);
      -ms-transform: translate(-12px, 0);
      -o-transform: translate(-12px, 0);
      transform: translate(-12px, 0);
  }
  .list1b li:after 
  {
      content: "";
      position: absolute;
      -webkit-transition-duration: 0.4s;
      transition-duration: 0.4s;
      -webkit-transition-property: width;
      transition-property: width;
      z-index: -1;
      background: #F0F5FB;
      height: 100%;
      left: 0;
      top: 0;
      width: 0;
  }
  .list1b li:hover:after 
  {
      width: 100%;
  }

  .more-button
   {
      height: 27px;
      border: 1px solid lightgrey;
      border-radius: 5px;
      color: darkgray;
      background-color: white;
	    float: right;
	    margin: 10px 5px;
   }

   

   .my-box
   {
     
     .my-link
      {
        color: black;
        font-weight: bold;
      }

    transition: 300ms;
    &:hover
    {
      -webkit-box-shadow: 0px 0px 49px -20px rgba(51, 51, 51, 0.74);
      -moz-box-shadow: 0px 0px 49px -20px rgba(51, 51, 51, 0.74);
      box-shadow: 0px 0px 49px -20px rgba(51, 51, 51, 0.74);
    }
   }

   .my-row
   {
    padding-right: 24px;
    padding-left: 0;
    margin-right: 20px;
    margin-left: 20px;
    display:flex;
    align-items: center;
    margin:10px 0 10px;
   }

   .plashka 
   {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #F7F7F7;
    width:100%;
    height: 100%;
    text-align: center;

    &:hover
    {
      text-decoration: none;
      color: #780404;
    }
   }

  .image-container 
  {
    position: relative;
    width: 100%;
    margin: 2em 0;
  }
  
  .image-container .anim-img 
  {
    z-index: 1;
    -webkit-transition: all ease 500ms;
    -o-transition: all ease 500ms;
    transition: all ease 500ms;
  }
  
  .image-container .anim-img:hover 
  {
    -webkit-box-shadow: 2px 2px 30px 2px rgba(0, 0, 0, 0.3);
    box-shadow: 2px 2px 30px 2px rgba(0, 0, 0, 0.3);
    z-index: 10;
    -webkit-transform: scale(1.1);
    -ms-transform: scale(1.1);
    transform: scale(1.1);
    -webkit-transition: all ease 500ms;
    -o-transition: all ease 500ms;
    transition: all ease 500ms;
  }

  .center-img {
    display: block;
    margin: 0 auto;
  }

.pagination {
    display: inline-block;
}

.pagination a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
}

.pagination a.active {
    background-color: #4CAF50;
    color: white;
    border-radius: 5px;
}

.pagination a:hover:not(.active) {
    background-color: #ddd;
    border-radius: 5px;
}

.rightimg  {
  float: right; /* Выравнивание по правому краю  */ 
  margin: 7px 0 7px 7px; /* Отступы вокруг картинки */
 }

 .leftimg {
  float:left; /* Выравнивание по левому краю */
  margin: 7px 7px 7px 0; /* Отступы вокруг картинки */
 }

.content img {
  max-width: 100%;
} 

#carouselExampleControls2{
  height: 550px;
  overflow: hidden;
}

.link-doc
{
  padding-bottom: 300px;
}


@media (max-width: 500px) {
  .content {
    padding: 1em;
  }

  .link-doc
  {
    padding-bottom: 0px;
  }
}

@media (max-width: 992px) {
  .navbar-collapse {
    z-index: 1000;
    &.collapsing {
      height: 0px;
    }
  }
  .navbar .navbar-nav {
    align-items: stretch;
    height: auto;
    overflow: auto;
  }
  .navbar .nav-link {
    &:hover {
    }
  }
  .nav-item {
    flex-grow: 1;
    background-color: #333333;;
  }
  .greetings {
    margin-top: 1.5rem;
  }

  #carouselExampleControls2{
    height: 250px;
  }
}